.resultPage{
   padding: 10px 0;
}

.resultHeader{
   display: flex;
   flex-wrap: wrap;
   justify-content: space-between;
   align-items: center;
   gap: 15px;
   margin-bottom: 15px;
}

.testName{
   padding: 10px;
   border-radius: 25px;
   font-weight: 600;
   color: var(--white);
   background-color: var(--sky-blue);
   font-size: 23px;
}

.resultCounter{
   font-size: 20px;
}